<template>
	<div>
		<ul class="g_formList">
			
			<li class="formList_li"><span class="character"><b>&nbsp;</b> 上一任单位名称</span><input class="texts" type="text" placeholder=""></li>
			
			<li class="formList_li">
				<span class="character"><b>&nbsp;</b> 公司注册时间</span>
				<select class="text" name="">
					<option value="">请选择</option>
					<option value="">...</option>
					<option value="">....</option>
				</select>
			</li>
			
			<li class="hint"><p>该单位需在全国红盾网可查询，中国红盾网网址为http://www.hongdunwang.com.cn/</p></li>
			
			<li class="formList_li"><span class="character"><b>&nbsp;</b> 工作部门名称</span><input class="text" type="text" placeholder=""></li>
			
			
			<li class="formList_li"><span class="character"><b>&nbsp;</b> 工作岗位</span><input class="text" type="text" placeholder=""></li>
			
			<li class="formList_li">
				<span class="character"><b>&nbsp;</b> 单位所在市</span>
				<select class="text" name="">
					<option value="">请选择</option>
					<option value="">...</option>
					<option value="">....</option>
				</select>
			</li>
			
			<li class="formList_li">
				<span class="character"><b>&nbsp;</b> 工作时间</span>
				<span class="">开始</span>
				<select class="text_little" name="">
					<option value="">请选择</option>
					<option value="">{{date}}</option>
					<option value="">....</option>
				</select>
			</li>
			<li class="formList_li">
				<span style="float: left;">结束</span>
				<span class="con_center" style="float: left;">{{date}}</span>
			</li>
			
			<li class="hint"><p>根据鉴定中心审核要求各工作单位之间的间隔时间不得超过1个月，该时间根据已填信息自动默认获取</p></li>
			
			
			<li class="formList_li"><span class="character"><b>&nbsp;</b> 部门联系人</span><input class="text" type="text" placeholder=""></li>
			
			<li class="hint"><p>部门联系人要写全称，能证明你的工作事实</p></li>
			
			<li class="formList_li"><span class="character"><b>&nbsp;</b> 部门办公电话</span><input class="text" type="text" placeholder=""></li>
			
			<li class="hint"><p>必须是固话，必须能接通并证明你的工作事实</p></li>
		</ul>
		
		<div class="button_lack" >保存并返回</div>
		<div class="button_whole" >保存并返回</div>
		
		<Bottom></Bottom>
	</div>
</template>

<script>
	import Bottom from '../../components/Bottom/Bottom'
	export default{
		components:{
			Bottom
		},
		created(){
			
			this.getData()
			
		},
		data(){
			return {
				   date:this.$store.state.nowYear+"年"+this.$store.state.nowMonth+"月"
			}
		},
		created(){
			if(this.$store.state.nowMonth<10){
				this.date=this.$store.state.nowYear+"年0"+this.$store.state.nowMonth+"月"
			}
			
			
		},
		methods:{
			getData(){
				 var obj={
				 	"s":this.$store.state.sessionId,
				 	"a":1
				 	
				 }
				this.$Api.ExamUnitInfo(obj).then((res)=>{
					console.log(res)
					
				})
				
			}
		}
	}
</script>

<style scoped="scoped" lang="scss">
	@import "../../sass/baseSass.scss";
	
	input[type=text], input[type=password], input[type=submit], select { border: #cccccc 1px solid; height: .8rem; border-radius: .1rem; width: 100%; }
	input[type=text]:focus, input[type=password]:focus, input[type=submit]:focus, select:focus { border-color: #fa8a12; }
	input[type=text], input[type=password] { padding: 0 .1rem; }
	input[type=submit] { background-color: #fa8a12; text-align: center; width: 100%; }
	textarea { height: auto; min-height: 1.2rem; border: #cccccc 1px solid; border-radius: .1rem; padding: .2rem .3rem; width: 100%; }
	input:-moz-placeholder { color: #999999; }
	::-webkit-input-placeholder { color: #999999; }
	input[type=text], input[type=password] { -webkit-appearance: none; }
	
	.g_formList{padding: .3rem 0.3rem 0;
		.formList_li{padding-left: 2.3rem; position: relative; height: .7rem; margin-bottom: .3rem; font-size: .3rem; line-height: .7rem;
			.character{position: absolute; left: 0;color: #1a1a1a;font-weight: 600;
				b{color: #f32929;}
			}
			.character_r{float: right;margin-right: 0.55rem;}
			.texts{height: .7rem;padding:0 0.1rem;width: 3.5rem;float: right;margin-right: 0.55rem;}
			.text{height: .7rem;padding:0 0.1rem;width: 4rem;float: right;margin-right: 0.55rem;}
			.text_little{height: .7rem;padding:0 0.1rem;width: 3rem;float: right;margin-right: 0.55rem;}
			.con_center{padding-left: .55rem;}
		}
		.hint{margin-bottom: .3rem;
			p{font-size: .25rem;color: #fa8a12;padding:0 0.4rem 0 0.18rem;}
		}
		.formList_li_B{font-size: .3rem;height: .7rem;line-height: .7rem;margin-bottom: .3rem;
			.formList_li_div{display:inline-block;
				.character{color: #1a1a1a;font-weight: 600;}
				.text_c{width: 2.6rem;}
			}
			.div_padding{padding-left:0.2rem;}
		}
	}
	.button_lack{width: 6.9rem;height: 1rem;background-color: #ccc;font-size: 0.34rem;color: #FFFFFF;text-align: center;line-height: 1rem;border-radius:0.5rem ;margin:0.5rem auto 1.4rem;}
	.button_whole{width: 6.9rem;height: 1rem;background-color: #fa8a12;font-size: 0.34rem;color: #FFFFFF;text-align: center;line-height: 1rem;border-radius:0.5rem ;margin:0.5rem auto 1.4rem;}
</style>


